.Flashcard {
    min-height: 180px;
    flex: 2;
    perspective: 1000px;
    cursor: pointer;
}

.front,
.back {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    box-sizing: border-box;
    padding: 30px;
    border-radius: 6px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 6em;
    font-weight: bold;
    backface-visibility: hidden;
    transition: 0.4s;
    transform-style: preserve-3d;
}

.front {
    transform: rotateY(0deg);
    background: #ffffff;
    z-index: 2;
}

.back {
    transform: rotateY(-180deg);
    background: #704e2e;
    color: #fad4c0;
}

.flipped .back {
    transform: rotateY(0deg);
}

.flipped .front {
    transform: rotateY(180deg);
}
