/* CIRCULAR COUNTER */

svg {
    width: 100%;
    height: 100%;
}

svg circle {
    width: 100%;
    height: 100%;
    fill: none;
    stroke: #cfcfcd;
    stroke-linecap: round;
    transform-origin: center;
    transform: rotate(-90deg);
    transition: stroke-dashoffset 0.5s ease 0s;
}

.counter {
    position: relative;
    width: 150px;
    height: 150px;
    border-radius: 50%;
    background: none;
}

.counter .number {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 50%;
    font-weight: 700;
    font-size: 2em;
}

@media (max-width: 500px) {
    .counter {
        width: 120px;
        height: 120px;
    }

    .counter .number {
        font-size: 1.6em;
    }
}

@media (max-width: 300px) {
    .counter {
        width: 90px;
        height: 90px;
    }

    .counter .number {
        font-size: 1.2em;
    }
}

/* HOVER STATBOX */

.statbox {
    position: relative;
    display: inline-block;
    cursor: pointer;
}

.statbox .statbox-text {
    visibility: hidden;
    width: 100px;
    background-color: #704e2e;
    font-size: 14px;
    text-align: center;
    color: #ffffff;
    padding: 10px 6px;
    border-radius: 6px;
    bottom: 135%;
    left: 50%;
    margin-left: -56px;
    position: absolute;
    z-index: 1;
    display: flex;
    justify-content: space-around;
}

.statbox:hover .statbox-text {
    visibility: visible;
}

.statbox .statbox-text::after {
    content: ' ';
    position: absolute;
    top: 100%;
    left: 50%;
    margin-left: -5px;
    border-width: 5px;
    border-style: solid;
    border-color: #704e2e transparent transparent transparent;
}
